.tips{
    background: url("/sites/all/modules/custom/icf_lgbt_app/src/assets/tips-bg.png");
    background-size: cover;
    border: #ffd02d 5px solid;
    padding: 15px;
}
.tips a{
    color: #333333 !important;
}

.tips .button:hover{
    color: #333333 !important;
    border: 2px solid #000000;
}
.tips .button:before{
    content: "";
    position: absolute;
    z-index: 1;
    right: 5px;
    bottom: 5px;
    width: 0;
    height: 0;
    border-bottom: 20px solid #ffd02d;
    border-left: 20px solid transparent;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
}
.tips .button:hover:before{
    width: 100%;
    height: 100%;
    right: 0;
    bottom: 0;
    background-color: #ffd02d;
}

@media screen and (min-width:768px) and (max-width:1200px){
	.tips{
		height: 600px !important;
	}
}


